import * as Yup from "yup";

export const REQUEST_VALID_SCHEMA = Yup.object().shape({
	name: Yup.string().min(2, " ").required(" "),
	number: Yup.string().required(" "),
	email: Yup.string()
		.required(" ")
		.matches(/.+@.+\..+/, " Email"),
	link: Yup.string().required(" ").url(" ").min(2, " "),
});
