import styled from "styled-components/macro";
import logo from "../../../../assets/images/sk-design-logo.svg";

const StyledTextContent = styled.div`
	position: relative;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 40px;
	align-items: flex-start;
`;
const StyledTitle = styled.h2`
	all: unset;
	font-weight: 600;
	font-size: 24px;
	line-height: 150%;
	color: #353238;
`;
const StyledContentColumn = styled.div`
	max-width: 940px;
	display: flex;
	flex-direction: column;
	gap: 15px;
`;
const StyledLogo = styled.img`
	height: 70px;
`;
const StyledParagraph = styled.p`
	all: unset;
	color: #353238;
	font-size: 14px;
	line-height: 150%;
`;
const StyledLink = styled.a`
	all: unset;
	color: #0086a8;
`;

function RequestTextContent() {
	return (
		<StyledTextContent>
			<StyledLogo src={logo} alt="SKDesign logo" />
			<StyledTitle>      </StyledTitle>
			<StyledContentColumn>
				<StyledParagraph>
					 SK Design      ,  
					, -     ,      .
				</StyledParagraph>
				<StyledParagraph>
					     ,       
					    , , ,    .
				</StyledParagraph>
				<StyledParagraph>
					     {" "}
					<span>
						<StyledLink href="https://github.com/script696?tab=repositories" target="_blank">
							
						</StyledLink>
					</span>
					.              :
					   ,  3D  (  ) 
					       .
				</StyledParagraph>
			</StyledContentColumn>
		</StyledTextContent>
	);
}

export default RequestTextContent;
